import { useEffect, useState, useContext } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { SocketContext } from '../context/socket'
import pieces from '../assets/pieceStyles.json'

function Lobby() {
    
    const navigate = useNavigate()
    const socket = useContext(SocketContext)
    let params = useParams()
    const [players, setPlayers] = useState([])

    useEffect(() => {

        socket.emit('game:data:fetch', callback => {
            if (callback.ok) {
                //console.log(callback.room)
                setPlayers(callback.room.players)
            } else {
                console.log("room not found")
                navigate('/')
            }
        })

        socket.on('game:data:update', (data) => {
            console.log('receiving updates')
            setPlayers(data.players)
        })

        socket.on('game:starting', () => {
            console.log('starting game')
            navigate(`/${params.code}/game`)
        })

        return () => {
            socket.off('game:data:fetch')
            socket.off('game:data:update')
            socket.off('game:starting')
        }

    }, [socket])

    const handleStart = () => {
        socket.emit('game:begin')
    }

    return (
        <div className="min-h-full flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
            <div className="max-w-md w-full space-y-8">
                <div className="relative flex items-center">
                    <span className="flex-shrink mx-4 text-gray-700 font-bold text-4xl">🦷 { params.code }</span>
                </div>

                <div className="rounded-lg overflow-hidden outline outline-neutral-300 outline-1 bg-white   ">

                    <div className="py-5 px-3 h-56">

                        { players.map((player, i) => <PlayerCard key={i} name={player.name} style={pieces[i]} />) }

                    </div>

                    </div>

                    <div>
                        <button type="submit" onClick={handleStart} className="group relative w-full flex justify-center py-4 px-4 border border-transparent text-sm uppercase font-medium rounded-lg text-white bg-indigo-600 disabled:bg-slate-300 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                        <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                        </span>
                        Start Game
                        </button>
                    </div>

            </div>
        </div>
    )
}

function PlayerCard(props) {
    return (
        <div className="flex justify-between px-2 py-2">                    
            <div className="flex text-gray-700 text-xl font-semibold">
                <div className="px-5">{props.style.piece}</div>
                {props.name}</div>
            <p className="text-gray-500 text-xl font-thin pr-6">Team</p>
        </div>
    )
}

export default Lobby