import { useContext, useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { SocketContext } from "../context/socket"
import { motion, useAnimation } from "framer-motion"
import rules from '../assets/rules.json'
// Bringing all the states up to Game() and consolidating emit events only on Game component

function Game() {

    const navigate = useNavigate()
    const socket = useContext(SocketContext)
    const params = useParams()
    const calloutControls = useAnimation()

    // states
    const [game, setGame] = useState() // main game state, to sync with server
    const [moving, setMoving] = useState(false)
    const [pidx, setPidx] = useState()

    useEffect(() => {

        socket.emit('game:data:fetch', callback => {
            if (callback.ok) {
                console.log("fetching initial game data")
                //console.log(callback.room)
                setGame(callback.room)
                setPidx(callback.room.players.findIndex(p => p._id === socket.id))
            } else {
                console.log("disconnected")
                navigate('/')
            }
        })

        socket.on('game:data:update', data => {
            console.log("updating game data")
            //console.log(data)
            setGame(data)
        })

        return () => {
            socket.off('game:data:fetch')
            socket.off('game:data:update')
            socket.off('game:diceRoll')
            socket.off('game:diceRollComplete')
        }

    }, [socket])

    const diceRollAction = () => {
        setMoving(true)
        socket.emit('game:diceRoll')
    }

    const goTo14 = () => {
        setMoving(true)
        socket.emit('game:dev:14')
    }

    const submitAnswerAction = (a) => {
        console.log('submitAnswerAction ' + a)
        socket.emit('game:submitAnswer', a, callback => {
            // if incorrect, the server wont update motion so we need to update it here
            if (callback.ok) {
                socket.emit('game:nextTurn')
                setMoving(false)
            }
        })
    }

    const onAnimComplete = async () => {   

        if (!moving) return

        console.log(`arrived at tile ${game.players[pidx].position}`)

        let rule = rules.find(r => r.tile === game.players[pidx].position)

        if (rule) {

            // play callout animations
            await calloutControls.start({ 
                fontSize: ['0rem','4.5rem','4.5rem','4.5rem'], 
                opacity: [0, 1, 1, 0],
                transition: { duration: 1, times: [0, 0.2, 0.7, 1] }
            })
            
            console.log(`rule found for tile ${game.players[pidx].position}`)
            console.log(rule)
            socket.emit('game:event', rule)
            return
        }

        socket.emit('game:nextTurn')
        setMoving(false)
        // console.log('animation complete')
        
    }

    return (
        <div className="bg-red-50 flex flex-col max-h-screen max-w-screen-xl min-w-fit m-auto gap-2 h-screen w-screen">
            
            <header className="bg-green-50 h-24 relative flex items-center mt-4">
                <img src="/um-lg.png" alt="Um Toothpoly" className="h-12"/>
                <span className="flex-shrink mx-4 text-gray-700 font-bold text-2xl">UNIVERSITI MALAYA TOOTHPOLY 🦷</span>
                <span>{ game?.code }</span>
                <span className="ml-auto">LEAVE GAME</span>
            </header>

            <main className="bg-slate-200 flex flex-col md:flex-row gap-2 h-full">

                <Stage 
                    game={game}
                    moving={moving}
                    calloutControls={calloutControls}
                    submitAnswerAction={submitAnswerAction} 
                    onAnimComplete={onAnimComplete} />

                <Side game={game} diceRollAction={diceRollAction} goTo14={goTo14} />

            </main>

            <footer className="uppercase h-14 relative flex items-center text-slate-400">&copy; {new Date().getFullYear()} Universiti Malaya</footer>

        </div>
    )
}

const Stage = ({game, moving, calloutControls, submitAnswerAction, onAnimComplete}) => {

    return (
        <div className="bg-yellow-50 basis-3/4 relative">
            <div className="board relative">
                
                <Callout calloutControls={calloutControls} game={game} />

                <div className="relative h-full w-full">
                { game?.players?.map((p, i) => <Piece anim={p.motion} idx={i} onAnimComplete={onAnimComplete}  key={i}/>) }
                </div>
            </div>        
            <Question game={game} moving={moving} submitAnswerAction={submitAnswerAction} />
        </div>
    )
}

const Callout = ({game, calloutControls}) => {

    return(
        <div className="absolute flex flex-col place-content-center left-1/2 -translate-x-1/2 bottom-1/2 translate-y-1/2">
            <motion.div className="font-marker text-center text-slate-800 text-[0rem]" animate={calloutControls} initial={false}>
                {rules.find(r => r.tile === game?.players[game?.turn]?.position)?.event}!
            </motion.div>
        </div>
    )
}

const Piece = ({anim, idx, onAnimComplete}) => {

    return (
        <motion.div className={`            
            absolute
            translate-x-[-50%]
            translate-y-[50%]
            bg-black
            piece
            player-${idx}
            rounded-full`}        
            animate={anim}
            transition={{ duration: anim.left.length * 0.25 }}
            initial={false}
            onAnimationComplete={onAnimComplete}
            />

    )
}

const Question = ({game, moving, submitAnswerAction}) => {

    const [answered, setAnswered] = useState(false)
    const buttonControls = useAnimation()

    let lang = 'en'

    return (
        <div className={`${game?.scene !== 'event' ? `invisible`: ""} card absolute flex flex-col place-content-center p-10 top-2/4 left-2/4 -translate-x-1/2 -translate-y-1/2 w-11/12 h-5/6 bg-white border-8 border-black`}>
            <span className="text-sm uppercase tracking-wide">Question for { game?.players[game?.turn].name }</span>
            <img src="/question-assets/dental-jaw-blue-overlay-1200x628-iStock-1145063557.jpg" alt="Dental Jaw" className="h-64 rounded-lg outline-offset-2 outline-1 outline-dashed outline-gray-500 m-auto"/>
            <span className="text-xl tracking-wide">{ game?.question[lang]?.question }</span>
            <motion.div className="flex flex-row my-8 mx-24 gap-8" animate={buttonControls}>
                { game?.question[lang]?.answers.map( (ans, i) => <button disabled={!moving} onClick={async () => {
                    setAnswered(true)
                    await buttonControls.start({ scale: [1, 1.1, 1], transition: { duration: 0.7} })
                    submitAnswerAction(ans.correct)
                    setAnswered(false)
                    }} className={`flex-1 text-center p-4 card hover:outline-4 disabled:bg-red-200 ${ answered ? ans.correct ? `bg-green-400` : `bg-red-400` : ``}`}>{ans.value}</button> ) }
            </motion.div>
        </div>
    )
}

const Side = ({game, diceRollAction, goTo14}) => {
    return (
        <aside className="bg-blue-50 flex-1 flex gap-2 flex-col">
            <Leaderboard players={game?.players} />
            <Dice />
            <Controls game={game} diceRollAction={diceRollAction} goTo14={goTo14}/>
        </aside>
    )
}

const Leaderboard = ({players}) => {
    
    const socket = useContext(SocketContext)
    let medal = ['🥇', '🥈', '🥉', '🥊']

    return(
        <div className="card flex flex-col p-4 flex-wrap gap-2 basis-1/2">
            <p className="font-bold text-xl mb-4">🏆 Leaderboard </p>
            { (players ? [...players] : [])?.sort((a, b) => b.position - a.position).map((player, i) => {
                return (
                    <div key={i} className={`${socket.id === player._id ? `font-bold bg-amber-100 rounded-xl py-1`: ``} flex flex-row gap-2`}>
                        <span className={`text-lg mr-2`}>{medal[i]}</span>
                        <span className="text-lg flex-1">{player.name} </span>
                        <span className="text-lg mr-4">📍{player.position}</span>
                    </div>
                )
            } ) }
        </div>
    )
}

const Dice = () => {
    return(
        <div className="card flex flex-col flex-wrap gap-2 p-4 flex-1">
            <p className="font-bold text-xl">🎲 Dice </p>
        </div>
    )
}

const Controls = ({game, diceRollAction, goTo14}) => {

    const socket = useContext(SocketContext)
    
    const myTurn = game?.players[game.turn]._id === socket.id
    const moving = game?.scene === 'moving' || game?.scene === 'event'
    let prevMove = game?.players[game.turn]
    let whoIsMoving = prevMove?._id === socket.id ? `Moving` : `${prevMove?.name} is moving`

    return(
        <div className="card flex flex-col flex-wrap place-content-center gap-2 flex-1">              
            {/* <p className="font-bold text-xl text-center mb-4">It's {game?.players[game.turn].name} turn!</p> */}
            {/*  */}
            <button onClick={diceRollAction} disabled={!myTurn || moving} className="text-center text-lg uppercase font-semibold p-4 card disabled:outline-red-400 disabled:bg-slate-400 disabled:hover:outline-1 outline-green-400 bg-green-300 hover:outline-4">
                {moving ? `${whoIsMoving}` : myTurn ? 'Roll the dice' : `${game?.players[game.turn].name}'s turn`}
            </button>

            {/* DEV */}
            <button onClick={goTo14} disabled={!myTurn || moving} className="text-center text-lg uppercase font-semibold p-4 card disabled:outline-red-400 disabled:bg-slate-400 disabled:hover:outline-1 outline-green-400 bg-green-300 hover:outline-4">
                {moving ? `${whoIsMoving}` : myTurn ? 'Go To 14' : `${game?.players[game.turn].name}'s turn`}
            </button>
            {/* DEV */}

        </div>
    )
}

export default Game