import { useState, useContext } from 'react'
import { SocketContext } from '../context/socket'
import { useNavigate } from 'react-router-dom'

function Lobby() {

    const navigate = useNavigate()
    const socket = useContext(SocketContext)
    const [roomField, setRoomField] = useState("")
    const [roomFound, setRoomFound] = useState(true)

    const handleNewGame = (e) => {
        console.log('%cLOBBY', 'color: #00ff00;', 'new game');

        socket.emit('game:new', callback => {
            navigate(`/${callback.code}`) // go to room onboarding
        })
        e.preventDefault()
    }

    const handleJoinGame = (e) => {

        // can be simplified, let room checks happen on the onboard page
        socket.emit('validate:room', {code: roomField}, callback => {
            if(callback.ok) {
                navigate(`/${roomField}`)
            } else {
                console.log('room not found')
                setRoomFound(false)
            }
        })

        e.preventDefault()
    }

    return (
        <div className="min-h-full flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
            <div className="max-w-md w-full space-y-8">
                <div className="relative flex items-center mb-14">
                    <img src="/um-lg.png" alt="Um Toothpoly" className="h-12"/>
                    <span className="flex-shrink mx-4 text-gray-700 font-bold text-2xl">UNIVERSITI MALAYA TOOTHPOLY 🦷</span>
                </div>

                <>            
                    <div>
                        <button type="submit" onClick={e => handleNewGame(e)} className="group relative w-full flex justify-center py-8 px-4 border border-transparent text-2xl uppercase font-medium rounded-lg text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                        🦷 New Game
                        </button>                
                    </div>
                    <div className="relative flex items-center">
                        <div className="flex-grow border-t border-gray-400"></div>
                        <span className="flex-shrink mx-4 text-gray-400">OR</span>
                        <div className="flex-grow border-t border-gray-400"></div>
                    </div>
                    <form className="mt-8 space-y-6" onSubmit={e => handleJoinGame(e)}>
                        
                        {/* <input type="hidden" name="roomField" /> */}
                        <div className="rounded-md shadow-sm -space-y-px">
                            <div>
                                <label className="sr-only">Room Code</label>
                                <input id="roomCode" name="roomCode" type="roomCode" autoComplete='off' required onChange={e => {setRoomField(e.target.value); setRoomFound(true)}} value={roomField} className="appearance-none relative block w-full px-4 py-6 border border-gray-300 placeholder-slate-400 text-gray-900 uppercase rounded-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 text-center" placeholder="ROOM CODE" />
                            </div>
                        </div>

                        <div>
                            <button type="submit" disabled={!roomField} className={`group relative w-full flex justify-center py-4 px-4 border border-transparent text-sm uppercase font-medium rounded-lg text-white ${roomFound ? `bg-indigo-600 hover:bg-indigo-700 focus:ring-indigo-500` : `bg-red-600`} disabled:bg-slate-300  focus:outline-none focus:ring-2 focus:ring-offset-2 `}>
                            <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                                {/* text-indigo-500 */}
                                { (!roomField || !roomFound) && 
                                    <svg className={`h-5 w-5 ${!roomField ? `bg-slate-300` : `bg-red-600`}`} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">                        
                                        <path fillRule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clipRule="evenodd" />
                                    </svg> 
                                }
                            </span>
                            { roomFound ? 'Join Room' : 'Room not found'}
                            </button>
                        </div>
                    </form>
                </>

            </div>
        </div>
    )
}

export default Lobby