import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import Onboard from './components/newOnboard'
import Lobby from './components/newLobby';
import Wait from './components/newWait';
import Game from './components/newGame';

import { SocketContext, socket } from './context/socket';

const App = () => (
	<SocketContext.Provider value={socket}>
		<BrowserRouter>
			<Routes>

				<Route path="/" exact element={<Lobby />} />
				<Route path="/:code" element={<Onboard />} />
				<Route path="/:code/wait" element={<Wait />} />
				<Route path="/:code/game" element={<Game />} />

			</Routes>
		</BrowserRouter>
	</SocketContext.Provider>
)

export default App;
